import React from "react";
import MultiCartItemAttributeComponent from "./MultiCartItemAttributeComponent";

export default class MultiCartItemDetailMobileComponent extends MultiCartItemAttributeComponent {
    constructor (props) {
        super(props);
    }

    renderTitle(title, price) {
        const output = [
            <div key={"pricePresentation"}>
                {title.replace('%1', this.getPartsOfPrice(price.price).join(',') + ' ' + price.currency)}
            </div>,
            <del key="alternatePrice" className="h4 u-font-small g-gray5-c u-inline-block">
                {price.crossedOut}
            </del>
        ];
        return (
            output
        )
    }
    render() {
        return (
            <div className={this.props.className} key={"mobileItem"}>
                { this.props.title ?
                    (this.props.price ? this.renderTitle(this.props.title, this.props.price) : this.props.title)
                    :
                    this.props.children
                }
            </div>
        )
    }
}